const InterviewCard = ({interview}: {interview: Interview}) => {
    return (
        <div className="relative w-full h-full space-y-5 px-5 py-2 blue-gradient-dark rounded-lg flex flex-col">
            <div className="absolute right-0 top-0 px-5 py-2 bg-light-400 rounded-md overflow-hidden">
                <p>{interview.type}</p>
            </div>

            <div className="relative size-20 object-cover rounded-full">
                <Image src={'/covers/adobe.png'} alt={'ttt'} fill />
            </div>

            <div>
                <h4>{interview.role}</h4>
            </div>

            <div>
                <DateLogo className="size-3 text-dark-400"/>
            </div>
        </div>
    )
}
export default InterviewCard
